<template>
  <div>
    <div class="notes__item">
      <div class="notes__item-top" :class="{ active: !view }">
        <h3 class="notes__item-top-title">{{ note.title }}</h3>
        <p class="notes__item-top-date">{{ note.data }}</p>
      </div>
      <div class="notes__item-text">
        {{ note.text }}
      </div>
      <div class="notes__item-btns">
        <button
          class="notes__item-btn edit"
          @click="$emit('editNote', note.id)"
        >
          <img src="@/assets/img/edit.svg" alt="" />
          <span>{{ $t('add') }}</span>
        </button>
        <button class="notes__item-btn del" @click="$emit('delNote', note.id)">
          <img src="@/assets/img/del.svg" alt="" />
          <span>{{ $t('delete')}}</span>
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    note: {
      typeof: Object,
    },
    view: {
      typeof: Boolean,
    },
  },
};
</script>

