<template>
  <div>
    <div class="notes">
      <div class="container">
        <div class="notes__top">
          <h2 class="notes__top-title">{{notes.length > 0 ?  $t('todo') : $t('notodo')}}</h2>
          <button class="notes__top-btn" @click="view = !view">
            <img v-show="view" src="@/assets/img/tabl.svg" alt="" />
            <img v-show="view == false" src="@/assets/img/setka.svg" alt="" />
            <span>{{ view ? $t('list') : $t('grid')}}</span>
          </button>
        </div>
        <div class="notes__list" :class="{ active: !view }"> 
          <NotesItem
            v-for="note in notes"
            :key="note.id"
            :note="note"
            :view="view"
            @delNote="$emit('delNote', note.id)"
            @editNote="$emit('editNote', note.id)"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import NotesItem from "@/components/NotesItem.vue";
export default {
  props: {
    notes: {
      typeof: Array,
    },
  },
  components: {
    NotesItem,
  },
  data() {
    return {
      view: true,
    };
  },
};
</script>
